<template>
  <div class="tab">
    <el-table
      ref="multipleTable"
      :data="searchDataList(searchKey)"
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55" show-header="false">
      </el-table-column>
      <el-table-column
        prop="name"
        label="姓名"
        width="80"
        align="center"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        prop="age"
        label="年龄"
        show-overflow-tooltip
        width="80"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="gender"
        label="性别"
        show-overflow-tooltip
        width="80"
        align="center"
      >
        <template slot-scope="scope">
          <span>{{ scope.row.gender == "male" ? "男" : "女" }}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="tel"
        label="联系电话"
        show-overflow-tooltip
        width="180"
        align="center"
      ></el-table-column>
      <el-table-column prop="address" label="详细地址" show-overflow-tooltip>
        <template slot-scope="scope">
          <span>{{ scope.row.city + scope.row.address }}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="160px" align="center">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"
            >编辑</el-button
          >
          <el-button size="mini" @click="handleDelete(scope.$index, scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <div style="margin-top: 20px">
      <el-button @click="handleSelectionDelete()">批量删除</el-button>
    </div>
  </div>
</template>

<script>
import updateUser from "./updateUser.vue";

export default {
  name: "tabList",
  props: {
    list: Array,
  },
  components: {
    updateUser,
  },
  data() {
    return {
      searchKey: "",
      tableData: [
        {
          id: 1,
          name: "王小虎",
          age: 14,
          gender: "male",
          tel: "13452513214",
          city: "广东省广州市海珠区",
          address: "金沙江路32",
        },
        {
          id: 2,
          name: "王小虎",
          age: 142,
          gender: "female",
          tel: "13452513214",
          city: "广东省东莞市东城区",
          address: "天地第一字号",
        },
      ],
      oldTableData: [],
      multipleSelection: [],
    };
  },
  mounted() {
    this.tableData.map((item, index) => {
      this.$set(this.oldTableData, index, { ...item });
    });

    // 新增、修改用户
    this.$EventBus.$on("UpdateUserInfo", (opt) => {
      this.tableData.map((item, index) => {
        this.$set(this.oldTableData, index, { ...item });
      });
      let isHas = this.tableData.findIndex((item) => {
        return item.id == opt.id;
      });

      if (isHas != -1) {
        this.$set(this.tableData, isHas, { ...opt });
      } else {
        this.tableData.push(opt);
      }
      // console.log(this.oldTableData);
      // console.log(this.tableData);
    });

    // 搜索
    this.$EventBus.$on("searchKey", (searchKey) => {
      //修改searchKey值，从而触发Vue的响应式，从而修改渲染列表。
      this.searchKey = searchKey;
    });

    // 撤销
    this.$EventBus.$on("widthdraw", () => {
      this.$confirm("确定撤回上一步吗？")
        .then((_) => {
          this.tableData = this.oldTableData;
        })
        .catch((err) => {
          console.log(err);
        });
    });
  },
  updated() {
    // console.log(this.tableData);
    // console.log(this.oldTableData);
  },
  computed: {},
  methods: {
    //返回符合搜索关键字的数据对象
    searchDataList(key) {
      return this.tableData.filter((item) => {
        let flag =
          item.name.includes(key) ||
          item.city.includes(key) ||
          item.address.includes(key);
        if (flag) {
          // item.address = item.city + item.address;
          let obj = {
            id: item.id,
            name: item.name,
            gender: item.gender,
            address: item.city + item.address,
            age: item.age,
          };
          return obj;
        }
      });
    },

    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },

    //当选项被选中的时候，更新multipleSelection
    handleSelectionChange(e) {
      this.multipleSelection = e;
    },

    //批量删除用户信息
    handleSelectionDelete() {
      this.multipleSelection.map((mulItem) => {
        this.tableData.map((tabItem, index) => {
          if (tabItem.id == mulItem.id) {
            this.handleDelete(index);
          }
        });
      });
    },

    //编辑用户信息
    handleEdit(index, row) {
      this.$EventBus.$emit("changeDialog", {
        type: "update",
        row,
      });
    },
    //删除用户信息
    handleDelete(index, row) {
      this.tableData.splice(index, 1);
    },
  },
};
</script>

<style scoped>
.el-button {
  padding: 5px 10px;
  font-size: 12px;
  border-radius: 0 !important;
}
</style>
