<template>
  <div class="manageUser">
    <div class="handleDiv">
      <div class="leftHand">
        <el-button @click="handleNewUser">新建</el-button>
        <el-input
          v-model="search"
          @input="searchKey"
          placeholder="按关键字搜索"
        ></el-input>
      </div>

      <div class="rightHand">
        <el-button class="rbtn" @click="withdraw">撤销</el-button>
      </div>
    </div>
    <TabList />
  </div>
</template>

<script>
import TabList from "./tab.vue";

export default {
  name: "manageUser",
  props: {},
  components: {
    TabList,
  },
  data() {
    return {
      search: "",
      searchTimer: null,
    };
  },
  methods: {
    //添加新用户
    handleNewUser() {
      this.$EventBus.$emit("changeDialog", {});
    },

    //搜索关键字
    searchKey(e) {
      clearTimeout(this.searchTimer);
      this.searchTimer = setTimeout(()=> {
        this.$EventBus.$emit("searchKey",e);
      },800)
    },

    //撤销
    withdraw() {
      this.$EventBus.$emit("widthdraw");
    }
  },
};
</script>

<style scoped>
.manageUser {
  width: 1000px;
  margin: 0 auto;
  margin-top: 10px;
}
.handleDiv {
  display: flex;
  justify-content: space-between;
}
.el-button,
.el-input {
  padding: 5px 10px;
}
.el-input,
::v-deep .el-input__inner {
  display: inline-block;
  width: 240px;
  height: 26px !important;
  line-height: 26px !important;
}
.el-button,
.el-input,
::v-deep .el-input__inner {
  font-size: 12px;
  border-radius: 0 !important;
}
</style>
