<template>
  <div class="updateUser">
    <el-drawer
      :title="my_title + '用户'"
      :before-close="handleClose"
      :visible.sync="dialog"
      direction="ltr"
      custom-class="demo-drawer"
      ref="drawer"
    >
      <div class="demo-drawer__content">
        <el-form :model="form">
          <div class="clear">
            <el-form-item label="名称" :label-width="formLabelWidth">
              <el-input v-model="form.name" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="性别" :label-width="formLabelWidth">
              <el-select v-model="form.gender">
                <el-option label="男" value="male"></el-option>
                <el-option label="女" value="fumale"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="年龄" :label-width="formLabelWidth">
              <el-input v-model="form.age" type="number" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="电话" :label-width="formLabelWidth">
              <el-input v-model="form.tel" type="number" autocomplete="off"></el-input>
            </el-form-item>
          </div>
          <div class="city_address">
            <p>详细地址</p>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-cascader
                :options="options"
                size="middle"
                v-model="form.city"
                placeholder="广东省广州市越秀"
                @change="handleChange"
              ></el-cascader>
              <el-input
                v-model="form.address"
                placeholder="请输入详细地址"
                autocomplete="off"
                class="city_address_ex"
              ></el-input>
            </el-form-item>
          </div>
        </el-form>

        <div class="demo-drawer__footer">
          <el-button @click="cancelForm">取 消</el-button>
          <el-button
            type="primary"
            @click="$refs.drawer.closeDrawer()"
            :loading="loading"
            >{{ loading ? "提交中 ..." : "确 定" }}</el-button
          >
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<script>
export default {
  name: "updateUser",
  props: {},
  data() {
    return {
      my_title: "新建",
      type: "add",
      table: false,
      dialog: false,
      loading: false,
      Uid: 100,
      form: {
        id: null,
        name: "",
        gender: "",
        age:null,
        tel: null,
        city: "",
        address: "",
      },
      formLabelWidth: "80px",
      timer: null,
      options: [
        {
          value: "广东省",
          label: "广东省",
          children: [
            {
              value: "广州市",
              label: "广州市",
              children: [
                {
                  value: "越秀区",
                  label: "越秀区",
                },
                {
                  value: "天河区",
                  label: "天河区",
                },
                {
                  value: "海珠区",
                  label: "海珠区",
                },
                {
                  value: "白云区",
                  label: "白云区",
                },
              ],
            },
            {
              value: "东莞市",
              label: "东莞市",
              children: [
                {
                  value: "南城区",
                  label: "南城区",
                },
                {
                  value: "东城区",
                  label: "东城区",
                },
                {
                  value: "莞城区",
                  label: "莞城区",
                },
                {
                  value: "虎门区",
                  label: "虎门区",
                },
              ],
            },
          ],
        },
      ],
    };
  },
  computed: {},
  mounted() {
    this.$EventBus.$on("changeDialog", (opt) => {
      this.dialog = true;
      if (opt.type == "update") {
        this.type = "update"
        this.my_title = "修改";
        this.form = {...opt.row};
        this.form.gender == "male" ? "男" : "女";
      }else{
        this.type = "add"
        this.my_title = "新建";
        this.form = {};
      }
    });
  },
  methods: {
    handleClose(done) {
      if (this.loading) {
        return;
      }
      this.$confirm("确定要提交表单吗？")
        .then((_) => {
          let city = "";
          this.form.city.map((item) => {
            city += item;
          });
          this.form.city = city;
          if(this.type != "update"){
            this.form.id = this.Uid;
            this.Uid++;
          }
          if (this.form.gender == "男") {
            this.form.gender = "male"
          }else{
            this.form.gender = "fumale"
          }

          this.$EventBus.$emit("UpdateUserInfo", this.form);

          // console.log(this.form);
          
          this.loading = true;
          this.timer = setTimeout(() => {
            done();
            // 动画关闭需要一定的时间
            setTimeout(() => {
              this.loading = false;
            }, 30);
          }, 300);
        })
        .catch((err) => {
          alert("省区不能为空。")
        });
    },
    cancelForm() {
      this.loading = false;
      this.dialog = false;
      clearTimeout(this.timer);
    },
    handleChange(val) {},
  },
};
</script>

